/* Button
   ========================================================================== */
/*
** 1. Change the font styles in all browsers.
** 2. Remove the margin in Firefox and Safari.
*/
.button-reset {
  font-family: inherit;
  font-size: 100%;
  line-height: 1;
  margin: 0; }

/*
** Show the overflow in IE.
** 1. Show the overflow in Edge.
*/
.button-reset {
  overflow: visible; }

/*
** Remove the inheritance of text transform in Edge, Firefox, and IE.
** 1. Remove the inheritance of text transform in Firefox.
*/
.button-reset {
  text-transform: none; }

/*
** Correct the inability to style clickable types in iOS and Safari.
*/
.reset-button {
  -webkit-appearance: button; }

/*
** Remove the inner border and padding in Firefox.
**/
.button-reset::-moz-focus-inner {
  border-style: none;
  padding: 0; }

/*
** Restore the focus styles unset by the previous rule.
*/
.button-reset:-moz-focusring {
  outline: 1px dotted ButtonText; }

/*
** Remove default styling
*/
.button-reset {
  background-color: transparent;
  border: none;
  padding: 0; }

.button-reset:focus {
  outline: 0; }

/*
** Cursor.
*/
@media (max-width: 679px) {
  .button-reset {
    cursor: default; } }

@media (min-width: 680px) {
  .button-reset {
    cursor: pointer; } }

/* Link
   ========================================================================== */
/*
** Remove the gray background on active links in IE 10.
*/
.link-reset {
  background-color: transparent; }

/* Image
   ========================================================================== */
/**
 * Remove the border on images inside links in IE 10.
 */
.img-reset {
  border-style: none; }

.landing {
  align-items: center;
  background-color: #98BCBF;
  background-image: url("../assets/png/kuji.png");
  display: flex;
  flex-direction: column;
  font-family: Lalezar, cursive;
  height: 100vh;
  min-height: calc(38.05vw + 120px);
  justify-content: center;
  text-shadow: 1px 1px 2px #969696; }

.landing__description {
  left: 6%;
  margin-top: 30px;
  position: relative; }

.landing__description svg {
  height: 5vw; }

.landing__description_fill .landing__description_dark {
  animation: darkfill 0.4s forwards; }

.landing__description_fill .landing__description_light {
  animation: lightfill 0.4s forwards; }

.landing__main-image {
  height: 27.2vw;
  width: 45vw; }

.landing__name {
  font-size: 5vw;
  font-weight: normal;
  margin: 0;
  position: relative;
  right: 3%; }

.landing__name > :first-child {
  color: #f8f1e5; }

.landing__name > :last-child {
  color: #1a405f; }

.landing__name > :first-child::after {
  color: #1a405f;
  content: ',';
  margin-right: 10px; }

@keyframes darkfill {
  from {
    fill: rgba(26, 64, 95, 0);
    stroke-width: 3; }
  to {
    fill: #1a405f;
    stroke-width: 0; } }

@keyframes lightfill {
  from {
    fill: rgba(248, 241, 229, 0);
    stroke-width: 3; }
  to {
    fill: #f8f1e5;
    stroke-width: 0; } }

@media (max-width: 700px) {
  .landing {
    min-height: calc(49.7vw + 120px); }
  .landing__description {
    left: 1%;
    margin-top: 18px; }
  .landing__main-image {
    height: 40vw;
    width: 66.1vw; }
  .landing__name {
    font-size: 7vw;
    right: 0%; } }

@media (max-width: 599px) {
  .landing__description svg {
    height: 7vw; }
  .landing__name {
    font-size: 7.5vw; } }

@media (max-width: 499px) {
  .landing__description svg {
    height: 8vw; }
  .landing__name {
    font-size: 8.5vw; } }

@media (max-width: 399px) {
  .landing__name {
    font-size: 10vw; } }
